.slider-background{
  width: 100%;
  height: 400px;
  position: fixed;
  top: 0px;
  z-index: -1;
  background: black;
}
.slider-container{
  overflow: hidden;
  width: 66%;
  height: 100%;
  margin-left: auto;
  margin-right: auto;
}
.border{
  height: 100%;
  position: absolute;
  top: 0px;
  width: 66%;
}
.shadow-left{
  background: linear-gradient(to right, black, 15%, transparent);
  width: 150px;
  height: 100%;
  position: absolute;
  top: 0px; 
  left:0px; 
}
.shadow-right{
  background: linear-gradient(to left, black, 15%, transparent);
  width: 150px;
  height: 100%;
  position: absolute;
  top: 0px; 
  right:0px;  
}
.cover-text{
  position: absolute;
  display: table;
  top: 50%; 
  left: 50%;
  transform: translate(-50%, -50%);
  width: auto;
  color: white;
  border-radius: 15px;
  text-align: center;
  background-image: linear-gradient(to right, rgba(0, 0, 0, .1), rgba(0, 0, 0, .8), rgba(0, 0, 0, .1));
}
.cover-text h3{
  padding-left: 15px;
  padding-right: 15px;
}
.cover-text h2{
  padding-left: 15px;
  padding-right: 15px;
  text-align: center;
}
@media (max-width: 1024px) {
  .slider-container{
    width: 75%;
  }
  .border{
    width: 75%;
  }
}
@media (max-width: 700px) {
  .slider-container{
    width: 90%;
  }
  .border{
    width: 90%;
  }
}
.slider{
  height: 100%;
  width: 200%;
  display: inline-block;
}
.slider-animator{
  animation-name: slide;
  animation-duration: 2s;
  animation-fill-mode: forwards;
}
.slider-reset{
  height: 100%;
  width: 200%;
  display: inline-block;
  animation-name: slide;
  animation-duration: 2s;
  animation-fill-mode: forwards;
}
@keyframes slide{
  from {transform: translateX(0);}
  to {transform: translateX(-50%);}
}
.slider .paralax-slide{
  width: 50%;
  background-position: center;
  display: inline-block;
  height: 100%;
}
/*
  PARALAX BACKGROUND
*/
.paralax-background{
  width: 100%;
  height: 550px;
  position: fixed;
  top: 0px;
  z-index: -1;
}

.background-content{
  height: 100%;
}
.tile-box{
  width: 100%;
  height: 100%;
  display: flex;
  flex-flow: column;
  flex-wrap: wrap;
}

.tile{
  width: 33%;
  height: 269px;
  border: 1px;
  padding: 2px;
}
.tile img{
  width: auto;
  height: 100%;
  display: block;
}
@media (min-width: 1350px) {
  .tile{
    width: 25%;
  }
}
.abs-button{
  position: absolute;
}
/* flip card */
.flip-card {
  background-color: transparent;
  width: 100%;
  height: 100%;
  perspective: 1000px;
}

.flip-card-inner {
  position: relative;
  width: 100%;
  height: 100%;
  text-align: center;
  transition: transform 0.8s;
  transform-style: preserve-3d;
}

.flipped {
  transform: rotateY(180deg);
}

.flip-card-front, .flip-card-back {
  position: absolute;
  top:0px;
  width: 100%;
  height: 100%;
  backface-visibility: hidden;
  overflow: hidden;
}

.flip-card-back {
  transform: rotateY(180deg);
}

.homepage .content-container{
  margin-top: 400px;
  position: absolute;
}
.homepage .content{
  position: relative;
  padding-top: 5px;
}

.prof-pic-container{
  width: 200px;
  height:200px;
  position: absolute;
  top:-100px;
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 1px 1px 9px;
  color:black !important;
}
#name{
  margin: 0px;
}
.profile{
  position: absolute;
  top:0px;
  width: 250px;
  padding-top: 100px;
  display: flex;
  align-items: center;
  flex-flow: column;
  height: calc(100% - 100px);
  background-color: #0842cc;
}
.profile > * {
