}
.prof-pic{
  width: 100%;
  height: auto;
}
.sidebar{
  display: flex;
  height: 100%;
  align-items: flex-start;
  flex-flow: column;
  padding-left: 15px;
}
.sticky-sidebar{
  position: sticky; 
  top: 0px;
}
.sticky-sidebar ul{
  padding-left: 15px;
}
.homepage .content{
  background-color: white;
}
.anchor-link a{
  color:white;
}
.main{
  margin-left: 250px;
  padding-left: 15px;
  padding-right: 15px;
}


/* Hambuger Menu */
.hamburger-link{
  margin-top:5px;
  border-bottom: 1px solid #666;
  padding: 3px 0px 3px 25px;
  background:rgba(0, 20, 255, .5)
}
.hamburger-link.inner{
  margin-left: 25px;
  background: rgba(0, 0, 0, .1);
}
.hamburger-link:hover{
  background-color: rgba(138, 223, 82, 0.63);
}
.anchor-menu, .dd-menu{
  position: sticky;
  float: right;
  top:15px;
  right:15px;
  height: auto;
  z-index: 4;
}
.anchor-menu.open-menu{
  position: fixed;
  display: none;
}
.dd-menu{
  display: none;
}
.dd-menu.open-menu{
  display: block;
}
.anchor-menu img, .dd-menu img{
  height: 35px;
  width: 35px;
  position: absolute;
  top: 5px;
  right: 5px;
  transition: 0.3s;
}
.homepage.menu-open{
  transition: 0.8s;
  transform: translateX(-400px);
}
.dropdown-menu{
  display: flex;
  place-content: center space-evenly;
  flex-direction: column;
  z-index: 3;
  overflow: hidden;
  position: fixed;
  right: 0px;
  top: 0px;
  width:400px;
  height:calc(100% - 50px);
  transition: 0.8s;
  background-color:rgba(255, 255, 255);
  box-shadow: 0 0 1px black;
  font-size: 27px;
  padding-top: 50px;
  justify-content: flex-start;
}
.dropdown-inner{
  width:372px;
}
.dropdown-inner a{
  text-decoration: none;
}

.dropdown-menu li{
  padding-top: 10px;
  list-style: none;
}
.dropdown-menu a{
  color:black;
}
@media (min-width: 776px){
  .anchor-menu{
    visibility: hidden;
  }
}
@media (max-width: 776px){
  .main{
    margin-left:0px;
  }
  .profile{
    position: relative;
    width: 100%;
  }
  .about{
    text-align: center;
  }
  .sticky-sidebar{
    visibility: hidden;
    height: 0px;
  }

}
