/*Carousel*/

.carousel {
  position: relative;
  width: 500px;
  margin: 0 auto;
  height: auto;
  overflow: hidden;
  white-space: nowrap;
  /* background-color: #e0e0e0; */
  /* border: 1px solid black; */
  box-shadow: 0 0 1px #666;
}

.slide-wrapper {
  position: relative;
  height: 100%;
  width: 100%;
}

.slide {
  display: inline-block;
  height: 100%;
  width: 100%;
}
.slide img{
  max-width: 100%;
  max-height: 550px;
  margin-left: 50%;
  transform: translate(-50%);
}
.desc-wrapper{
  white-space: normal;
  margin:5px 15px 5px 15px;
  background-color: white;
  padding:1px 10px 1px 10px;
  border-radius: 5px;
  box-shadow: 0 0 1px black;
}
.current-slide{
  position: absolute;
  top:5px;
  right: 5px;
  background-color: rgba(255, 255, 255, 0.6);
  border-radius: 7px;
  padding: 0 2px 0 2px;
}
.arrow{
  height: 50px;
  width: 50px;
  position: absolute;
  top: 50%;
  border-radius: 25px;
  background-color: rgba(255, 255, 255, 0.4);
}
.arrow img {
  width: 100%;
  height: 100%;
  display: block;
  margin-left: auto;
  margin-right: auto;
  transform: translateX(9px);
}

.prev-arrow{
  transform: scaleX(-1);
  left:0px; 
}
.next-arrow{
  right: 0px;
}